.wrap {
  display: -webkit-flex;
  display: flex;
  padding: 0 15px;
}
.leftCol {
  width: 16%;
}
.rightCol {
  text-decoration: none;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding-left: 40px;
  width: 83.333%;
}
.selector {
  display: inline-block;
  margin-left: 10px;
}
.pname {
  margin-right: 10px;
}
.phone-list {
  border: 1px solid grey;
  border-radius: 5px;
  display: -webkit-flex;
  display: flex;
  margin-bottom: 10px;
  padding: 10px 4px 4px 4px;
  text-decoration: none;
}
.picphone {
  display: block;
  max-width: 100px;
  height: auto;
}
.phone-text {
  margin-left: 14px;
}
.description {
  font-family: 'Ubuntu', sans-serif;
  font-size: 12px;
}

.big-img {
  float: left;
  position: relative;
  max-width: 350px;
  height: auto;
  border: 1px solid #000;
  padding: 20px;
  margin: 10px 20px 10px 0;
  background: white;
}
.top-title {
  border-bottom: 1px solid gray;
  font-size: 36px;
  font-weight: 500;
  margin: 20px 0 10px 0;
  max-width: 100%;
}

.mini-img {
  max-width: 100px;
  max-height: auto;
  padding: 10px;
  border: 1px solid #000;
  margin: 20px;
}

.feature-data {
  max-width: 160px;
  padding: 0 30px 0 0;
}

.main-description {
  margin-top: 150px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.info-header {
  font-size: 20px;
  margin: 0;
  padding: 0;
}

.error {
  color: red;
  font-size: 36px;
  font-style: italic;
}

body {
  font-family: 'Ubuntu', sans-serif;
  font-size: 14px;
  line-height: 1.49;
  color: #333;
  padding: 20px;
  background: ffe;
  list-style: none;
}
a {
  text-decoration: none;
}

.main {
  padding: 0 15px;
}
.button {
  display: inline-block;
  border-radius: 4px;
  background-color: tomato;
  border: none;
  color: #ffffff;
  text-align: center;
  font-size: 12px;
  padding: 10px;
  width: 100px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  cursor: pointer;
  margin: 5px;
}

.button:hover {
  padding-right: 15px;
}

header {
  padding: 24px;
}

.layout {
  display: -webkit-flex;
  display: flex;
  height: 500px;
}

main {
  width: 75%;
  background: lightblue;
}

aside {
  width: 25%;
  background: lightcyan;
}

.nav-wrapper {
  background: tomato;
}

